* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

#main-canvas {
  border: 1px solid black;
}

#toolbox {
  position: absolute;
  width: 30vw;
  height: 90vh;
  top: 1em;
  right: 1em;
}

#prim-option-box {
  height: 25vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, 4em);
  grid-auto-rows: 4em;
  gap: 0.5em;
  overflow-y: auto;
}

#prim-used-box {
  height: 25vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 4em;
  gap: 0.5em;
  overflow-y: auto;
}

.image-button {
  background-color: transparent;
  border: 1px solid black;
}

.image-button:hover {
  outline: 1px solid black;
}

.image-icon {
  max-width: 100%;
  max-height: 100%;
}

.prim-entry {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
  border: 1px solid black;
  padding: 0 1em;
}

.prim-delete {
  justify-self: end;
}

.hidden {
  visibility: hidden;
}

#controls-set {
  display: flex;
  flex-direction: column;
}

.control-bar {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-items: center;
}